export default {
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
			infonum:this.item.infonum
		};
	},
	onLoad() {},
	methods: {
		isguanzhu() {
			this.item.isguanzhu = true;
			uni.showToast({
				title: '关注成功！'
			});
		},
		//操作
		caozuo(type) {
			console.log('点击了');
			switch (type) {
				case 'ding':
					if (this.item.infonum.index == 1) {
						return;
					}
					this.item.infonum.dingnum++;
					if (this.item.infonum.index == 2) {
						this.item.infonum.cainum--;
					}
					this.item.infonum.index = 1;
					break;
				case 'cai':
					if (this.item.infonum.index == 2) {
						return;
					}
					this.item.infonum.cainum++;
					if (this.item.infonum.index == 1) {
						this.item.infonum.dingnum--;
					}
					this.item.infonum.index = 2;
					break;
			}
		},
		//进入详情页
		opendetail(){
			console.log('进入详情页')
		},

		
	}
};
